.invisible {
	display: none!important;
}

.visible {
	display: block!important;
}

.styled-checkbox {
	position: absolute;
	opacity: 0;
}

.styled-checkbox+label {
	position: relative;
	cursor: pointer;
	padding: 0;
}

.styled-checkbox+label:before {
	content: '';
	margin-right: 10px;
	display: inline-block;
	vertical-align: text-top;
	width: 20px;
	height: 20px;
	background: white;
	border: 2px solid #aaaaaa;
}

.styled-checkbox:hover+label:before {
	background: #f35429;
}

.styled-checkbox:focus+label:before {
	box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
}

.styled-checkbox:checked+label:before {
	background: #f35429;
}

.styled-checkbox:disabled+label {
	color: #b8b8b8;
	cursor: auto;
}

.styled-checkbox:disabled+label:before {
	box-shadow: none;
	background: #ddd;
}

.styled-checkbox:checked+label:after {
	content: '';
	position: absolute;
	left: 5px;
	top: 10px;
	background: white;
	width: 3px;
	height: 3px;
	box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
	transform: rotate(45deg);
}

.number-input input[type="number"] {
	-webkit-appearance: textfield;
	-moz-appearance: textfield;
	appearance: textfield;
}

.number-input input[type=number]::-webkit-inner-spin-button,
.number-input input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
}

.number-input {
	margin-bottom: 3rem;
}

.number-input button {
	-webkit-appearance: none;
	background-color: transparent;
	border: none;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	margin: 0;
	position: relative;
}

.number-input button:before,
.number-input button:after {
	display: inline-block;
	position: absolute;
	content: '';
	height: 2px;
	transform: translate(-50%, -50%);
}

.number-input button.plus:after {
	transform: translate(-50%, -50%) rotate(90deg);
}

.number-input input[type=number] {
	text-align: center;
}

.number-input.number-input {
	border: 1px solid #ced4da;
	width: 10rem;
	border-radius: .25rem;
}

.number-input.number-input button {
	width: 2.6rem;
	height: .7rem;
}

.number-input.number-input button.minus {
	padding-left: 10px;
}

.number-input.number-input button:before,
.number-input.number-input button:after {
	width: .7rem;
	background-color: #495057;
}

.number-input.number-input input[type=number] {
	max-width: 4rem;
	padding: .5rem;
	border: 1px solid #ced4da;
	border-width: 0 1px;
	font-size: 1rem;
	height: 2rem;
	color: #495057;
}

@media not all and (min-resolution:.001dpcm) {
	@supports (-webkit-appearance: none) and (stroke-color:transparent) {
		.number-input.def-number-input.safari_only button:before, .number-input.def-number-input.safari_only button:after {
			margin-top: -.3rem;
		}
	}
}

.input-range__slider {
	appearance: none;
	background: #3f51b5;
	border: 1px solid #3f51b5;
	border-radius: 100%;
	cursor: pointer;
	display: block;
	height: 1rem;
	margin-left: -0.5rem;
	margin-top: -0.65rem;
	outline: none;
	position: absolute;
	top: 50%;
	transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
	width: 1rem;
}

.input-range__slider:active {
	transform: scale(1.3);
}

.input-range__slider:focus {
	box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
}

.input-range--disabled .input-range__slider {
	background: #cccccc;
	border: 1px solid #cccccc;
	box-shadow: none;
	transform: none;
}

.input-range__slider-container {
	transition: left 0.3s ease-out;
}

.input-range__label {
	color: #aaaaaa;
	font-family: "Helvetica Neue", san-serif;
	font-size: 0.8rem;
	transform: translateZ(0);
	white-space: nowrap;
}

.input-range__label--min,
.input-range__label--max {
	bottom: -1.4rem;
	position: absolute;
}

.input-range__label--min {
	left: 0;
}

.input-range__label--max {
	right: 0;
}

.input-range__label--value {
	position: absolute;
	top: -1.8rem;
}

.input-range__label-container {
	left: -50%;
	position: relative;
}

.input-range__label--max .input-range__label-container {
	left: 50%;
}

.input-range__track {
	background: #eeeeee;
	border-radius: 0.3rem;
	cursor: pointer;
	display: block;
	height: 0.3rem;
	position: relative;
	transition: left 0.3s ease-out, width 0.3s ease-out;
}

.input-range--disabled .input-range__track {
	background: #eeeeee;
}

.input-range__track--background {
	left: 0;
	margin-top: -0.15rem;
	position: absolute;
	right: 0;
	top: 50%;
}

.input-range__track--active {
	background: #3f51b5;
}

.input-range {
	height: 1rem;
	position: relative;
	width: 100%;
}

.l-title {
	text-shadow: 0px 1px 0px rgb(204, 204, 204), 0px 2px 0px rgb(201, 201, 201), 0px 3px 0px rgb(187, 187, 187), 0px 4px 0px rgb(185, 185, 185), 0px 5px 0px rgb(170, 170, 170), 0px 6px 1px rgba(0, 0, 0, 0.1), 0px 0px 5px rgba(0, 0, 0, 0.1), 0px 1px 3px rgba(0, 0, 0, 0.3), 0px 3px 5px rgba(0, 0, 0, 0.2), 0px 5px 10px rgba(0, 0, 0, 0.25), 0px 20px 20px rgba(0, 0, 0, 0.15);
	color: #FFFFFF;
	font-family: 'League Gothic', Impact, sans-serif;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	text-align: center;
	margin: 1px;
}

.l-desc {
	text-shadow: 0px 1px 3px black;
	font-weight: bold;
}

.effect2 {
	position: relative;
	margin-top: 0 !important;
}

.effect2:before,
.effect2:after {
	z-index: -1;
	position: absolute;
	content: "";
	bottom: 8px;
	left: 10px;
	width: 50%;
	top: 80%;
	max-width: 300px;
	background: #777;
	-webkit-box-shadow: 0 15px 10px #777;
	-moz-box-shadow: 0 15px 10px #777;
	box-shadow: 0 15px 10px #777;
	-webkit-transform: rotate(-3deg);
	-moz-transform: rotate(-3deg);
	-o-transform: rotate(-3deg);
	-ms-transform: rotate(-3deg);
	transform: rotate(-3deg);
}

.effect2:after {
	-webkit-transform: rotate(3deg);
	-moz-transform: rotate(3deg);
	-o-transform: rotate(3deg);
	-ms-transform: rotate(3deg);
	transform: rotate(3deg);
	right: 10px;
	left: auto;
}

.btn.btn-sm {
	padding: 0.5rem 1.1rem;
	font-size: 0.64rem;
}